  --switcher-theme: linear-gradient(180deg, #4f2ee8 0%, #dcdcdc 100%);
  --logo-fill-color: #000;
  --categories-text-color: #1119;
  --categories-text-color-active: #4f2ee8;
  --books-color: #b4afaf;
