@media screen and (max-width: 767px) {
  .modal-team, .modal-team-open-btn {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .modal-team-wrapper {
    opacity: 0;
    visibility: hidden;
    transition: opacity var(--transition), visibility var(--transition);
  }

  .modal-team-wrapper.is-open {
    opacity: 1;
    visibility: visible;
  }

  .modal-team-wrapper:before {
    content: "";
    background-color: var(--main-background-color);
    width: 100%;
    height: 615px;
    position: absolute;
    bottom: 0;
  }

  .modal-team-goit-icon {
    fill: var(--black);
    transition: fill var(--transition);
    position: absolute;
    top: -32px;
    left: 0;
  }

  .modal-team-goit:is(:hover, :focus) .modal-team-goit-icon {
    fill: #ff5c01;
  }

  .modal-team-open-btn {
    transition: fill var(--transition);
    background-color: #0000;
    border: none;
    padding: 20px;
    line-height: 1;
    position: absolute;
    bottom: 1px;
    left: 50%;
    transform: translateX(-50%);
  }

  .modal-team-open-btn:is(:hover, :focus) {
    fill: #ff5c01;
  }

  .modal-team-close-btn {
    background-color: #0000;
    border: none;
    padding: 0;
    display: flex;
    position: absolute;
    top: -36px;
    right: -5px;
  }

  .modal-team-close-icon {
    fill: #ff5c01;
    transition: fill var(--transition);
    justify-content: center;
    align-items: center;
  }

  .modal-team-close-btn:is(:hover, :focus) .modal-team-close-icon {
    fill: var(--violet);
  }

  .modal-team {
    z-index: 9999;
    background-color: var(--violet);
    border-radius: 15px;
    justify-content: center;
    padding: 12px;
    display: flex;
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
  }

  .modal-team-list {
    flex-wrap: wrap;
    justify-content: center;
    gap: 12px;
    width: 704px;
    display: flex;
  }

  .modal-team-item {
    transition: transform var(--transition), background-color var(--transition), box-shadow var(--transition);
    background-color: #ffffff08;
    border: 1px solid #ffffff26;
    border-radius: 8px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: calc(33.3333% - 8px);
    padding: 16px 10px;
    display: flex;
  }

  .modal-team-item:hover {
    background-color: #ffffff14;
    transform: translateY(-3px);
    box-shadow: 0 8px 8px #00000021;
  }

  .modal-team-photo {
    background-color: var(--white);
    transition: transform var(--transition), border-radius var(--transition);
    border-radius: 50%;
    width: 50px;
    height: 50px;
    overflow: hidden;
    transform: scale(1);
  }

  .modal-team-item:hover .modal-team-photo {
    border-radius: 10%;
    transform: scale(1.25);
  }

  .modal-team-title {
    text-align: center;
    color: var(--white);
    margin-top: 16px;
    font-size: 14px;
    line-height: 1;
  }

  .modal-team-text {
    color: #ffffffb3;
    margin-top: 4px;
    font-size: 11px;
    line-height: 1;
  }

  .modal-team-link {
    cursor: pointer;
    width: 24px;
    height: 24px;
    margin-top: 12px;
  }

  .modal-team-icon {
    fill: #ffffffb3;
    transition: transform var(--transition), fill var(--transition);
    transform: scale(1);
  }

  .modal-team-icon:is(:hover, :focus) {
    fill: var(--white);
    transform: scale(1.25);
  }
}

@media screen and (min-width: 1280px) {
  .modal-team-wrapper:before {
    height: 706px;
  }

  .modal-team {
    padding: 20px;
    bottom: 30px;
  }

  .modal-team-open-btn {
    bottom: 12px;
  }

  .modal-team-list {
    gap: 20px;
    width: 860px;
  }

  .modal-team-item {
    width: calc(33.3333% - 13.3333px);
  }

  .modal-team-photo {
    width: 60px;
    height: 60px;
  }

  .modal-team-title {
    font-size: 18px;
  }

  .modal-team-text {
    font-size: 14px;
  }
}

.gotop-wrapper {
  opacity: 0;
  transition: opacity var(--transition);
  position: fixed;
  bottom: 84px;
  left: 50%;
  transform: translateX(-50%);
}

.gotop-wrapper.show {
  opacity: 1;
}

.gotop-link {
  transition: transform var(--transition);
  background-color: #502ee84d;
  border-radius: 50%;
  width: 64px;
  height: 64px;
  display: block;
  scale: 1;
}

.gotop-link:before {
  content: "";
  background-color: var(--violet);
  border-radius: 50%;
  width: 54px;
  height: 54px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale3d(1, 1, 1);
}

.gotop-link:is(:hover, :focus) {
  transform: scale(1.1);
}

.gotop-icon {
  fill: var(--white);
  width: 24px;
  height: 24px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}









.js-content-container {
  height: 300px;
}

.pagination-container {
  justify-content: center;
  align-items: center;
  gap: 24px;
  display: flex;
}

.left-arrows-wrapper, .right-arrows-wrapper {
  gap: 10px;
  display: flex;
}

.js-pagination {
  gap: 10px;
  margin: 0;
  padding: 0;
  display: flex;
}

.js-pagination-button.start, .js-pagination-button.backward {
  background-color: var(--black);
  border: 1px solid var(--black);
  cursor: pointer;
  border-radius: 50%;
  width: 44px;
  height: 44px;
  position: relative;
}

.pagination-icon-start, .pagination-icon-backward {
  fill: var(--pagination-left-icon-color);
  width: 24px;
  height: 24px;
  position: absolute;
  bottom: 50%;
  right: 50%;
  transform: translate(-50%, -50%)rotate3d(0, 0, 1, 180deg);
}

.js-pagination-button.end, .js-pagination-button.forward {
  background-color: var(--yellow);
  border: 1px solid var(--black-2);
  cursor: pointer;
  border-radius: 50%;
  width: 44px;
  height: 44px;
  position: relative;
}

.pagination-icon-end, .pagination-icon-forward {
  fill: var(--black-2);
  width: 24px;
  height: 24px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.js-pagination-pages {
  color: var(--text);
  border: 1px solid var(--pagination-border-color);
  cursor: pointer;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.22;
  display: flex;
}

.js-active-pagination {
  color: var(--pagination-text-active-color);
  background-color: var(--pagination-border-color);
}





